import { FastifyInstance } from "fastify";

export default async function securityRoutes(app: FastifyInstance) {
  // Security overview
  app.get("/security", async () => {
    return {
      alerts: [],
      status: "ok",
    };
  });

  // Create a security alert (manual for MVP)
  app.post("/security/alerts", async (req) => {
    const { severity, description } = req.body as {
      severity: "low" | "medium" | "high" | "critical";
      description: string;
    };

    return {
      created: true,
      severity,
    };
  });

  // Resolve an alert
  app.post("/security/alerts/:id/resolve", async () => {
    return { resolved: true };
  });
}
