import { FastifyInstance } from "fastify";

export default async function wikiRoutes(app: FastifyInstance) {
  // Get wiki pages list or home page
  app.get("/wiki", async (req) => {
    const { owner, repo } = req.params as any;

    return {
      owner,
      repo,
      pages: [],
    };
  });

  // Create or update a wiki page
  app.post("/wiki", async (req) => {
    const { title, content } = req.body as {
      title: string;
      content: string;
    };

    return {
      saved: true,
      title,
    };
  });

  // Get a specific wiki page
  app.get("/wiki/:slug", async (req) => {
    const { slug } = req.params as any;

    return {
      slug,
      content: "",
    };
  });

  // Delete a wiki page
  app.delete("/wiki/:slug", async () => {
    return { deleted: true };
  });
}
