import { FastifyInstance } from "fastify";

import authRoutes from "./modules/auth/routes";
import userRoutes from "./modules/users/routes";
import repoRoutes from "./modules/repos/routes";
import marketplaceRoutes from "./modules/marketplace/routes";
import searchRoutes from "./modules/search/routes";
import aiRoutes from "./modules/ai/routes";
import apiTesterRoutes from "./modules/apiTester/routes";
import systemRoutes from "./modules/system/routes";

export async function registerRoutes(app: FastifyInstance) {
  app.register(systemRoutes);

  app.register(authRoutes, { prefix: "/api/v1/auth" });
  app.register(userRoutes, { prefix: "/api/v1/users" });
  app.register(repoRoutes, { prefix: "/api/v1/repos" });
  app.register(marketplaceRoutes, { prefix: "/api/v1/marketplace" });
  app.register(searchRoutes, { prefix: "/api/v1/search" });
  app.register(aiRoutes, { prefix: "/api/v1/ai" });
  app.register(apiTesterRoutes, { prefix: "/api/v1/api-test" });

  // If you want a global test route, define it **inside this function**
  app.get("/", async (req, reply) => {
    return { status: "API is running" };
  });
}
