import { FastifyInstance } from "fastify";

export default async function filesRoutes(app: FastifyInstance) {
  app.get("/tree/*", async () => ({}));
  app.get("/blob/*", async () => ({}));
  app.post("/blob", async () => ({ saved: true }));
  app.delete("/blob/*", async () => ({ deleted: true }));

  app.get("/archive.zip", async () => {
    return { zip: true };
  });
}
