import { FastifyInstance, FastifyRequest, FastifyReply } from "fastify";
import { authMiddleware } from "../middleware/auth.js";

import filesRoutes from "./sub/files.js";
import commitsRoutes from "./sub/commits.js";
import issuesRoutes from "./sub/issues.js";
import pullsRoutes from "./sub/pulls.js";
import actionsRoutes from "./sub/actions.js";
import insightsRoutes from "./sub/insights.js";
import wikiRoutes from "./sub/wiki.js";
import securityRoutes from "./sub/security.js";
import settingsRoutes from "./sub/settings.js";
import chatRoutes from "./sub/chat.js";

import { readJSON, writeJSON, generateId } from "../lib/jsonDb.js";

interface RepoMember {
  userId: string;
  role: "OWNER" | "WRITE" | "READ";
}

interface Repo {
  id: string;
  name: string;
  slug: string;
  ownerId: string;
  visibility: "PUBLIC" | "PRIVATE";
  members: RepoMember[];
  createdAt: string;
}

interface CreateRepoBody {
  name: string;
  visibility?: "PUBLIC" | "PRIVATE";
}

export default async function repoRoutes(app: FastifyInstance) {
  app.addHook("preHandler", authMiddleware);

  // CREATE REPO
  app.post("/", async (req: FastifyRequest<{ Body: CreateRepoBody }>, reply: FastifyReply) => {
    const { name, visibility = "PUBLIC" } = req.body;
    const repos: Repo[] = readJSON("repos.json");

    const newRepo: Repo = {
      id: generateId(),
      name,
      slug: name.toLowerCase().replace(/\s+/g, "-"),
      ownerId: (req as any).user.id, // make sure authMiddleware sets req.user
      visibility,
      members: [{ userId: (req as any).user.id, role: "OWNER" }],
      createdAt: new Date().toISOString(),
    };

    repos.push(newRepo);
    writeJSON("repos.json", repos);

    return reply.send({ created: true, repo: newRepo });
  });

  // GET REPO
  app.get("/:owner/:repo", async (req: FastifyRequest<{ Params: { owner: string; repo: string } }>, reply: FastifyReply) => {
    const { owner, repo } = req.params;
    const repos: Repo[] = readJSON("repos.json");

    const found = repos.find((r) => r.ownerId === owner && r.slug === repo);
    if (!found) return reply.status(404).send({ error: "Repo not found" });

    return reply.send(found);
  });

  // UPDATE REPO
  app.patch("/:owner/:repo", async (req: FastifyRequest<{ Params: { owner: string; repo: string }; Body: Partial<Repo> }>, reply: FastifyReply) => {
    const { owner, repo } = req.params;
    const repos: Repo[] = readJSON("repos.json");

    const index = repos.findIndex((r) => r.ownerId === owner && r.slug === repo);
    if (index === -1) return reply.status(404).send({ error: "Repo not found" });

    const updated: Repo = { ...repos[index], ...req.body };
    repos[index] = updated;
    writeJSON("repos.json", repos);

    return reply.send(updated);
  });

  // DELETE REPO
  app.delete("/:owner/:repo", async (req: FastifyRequest<{ Params: { owner: string; repo: string } }>, reply: FastifyReply) => {
    const { owner, repo } = req.params;
    let repos: Repo[] = readJSON("repos.json");

    const beforeCount = repos.length;
    repos = repos.filter((r) => !(r.ownerId === owner && r.slug === repo));

    if (repos.length === beforeCount) return reply.status(404).send({ error: "Repo not found" });

    writeJSON("repos.json", repos);
    return reply.send({ deleted: true });
  });

  // Register submodules
  app.register(filesRoutes, { prefix: "/:owner/:repo" });
  app.register(commitsRoutes, { prefix: "/:owner/:repo" });
  app.register(issuesRoutes, { prefix: "/:owner/:repo" });
  app.register(pullsRoutes, { prefix: "/:owner/:repo" });
  app.register(actionsRoutes, { prefix: "/:owner/:repo" });
  app.register(insightsRoutes, { prefix: "/:owner/:repo" });
  app.register(wikiRoutes, { prefix: "/:owner/:repo" });
  app.register(securityRoutes, { prefix: "/:owner/:repo" });
  app.register(settingsRoutes, { prefix: "/:owner/:repo" });
  app.register(chatRoutes, { prefix: "/:owner/:repo" });
}
